.offer {
  padding: 186px 60px 15px;
  text-align: center;
  background-color: $color-primary-500;
  background-image: url("../img/offer/icon_gift.svg"),
    url("../img/offer/catbag-small.jpg");
  background-repeat: no-repeat;
  background-size: auto, cover;
  background-position: 50% 58px, 50% 50%;
  color: $color-white;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  @media (min-resolution: 2dppx) {
    background-image: url("../img/icons/icon_gift.svg"),
      url("../img/offer/catbag-small@2x.jpg");
  }
  @media (min-width: $breakpoint-medium) {
    padding: 70px 120px 70px 442px;
    background-position: 77px 50%, 50% 50%;
    text-align: left;
    background-image: url("../img/offer/icon_gift.svg"),
      url("../img/offer/catbag-medium.jpg");
    @media (min-resolution: 2dppx) {
      background-image: url("../img/icons/icon_gift.svg"),
        url("../img/offer/catbag-medium@2x.jpg");
    }
  }
  @media (min-width: $breakpoint-large) {
    padding: 178px 40px 48px;
    background-position: 50% 50px, 50% 50%;
    text-align: center;
    background-image: url("../img/offer/icon_gift.svg"),
      url("../img/offer/catbag-large.jpg");
    @media (min-resolution: 2dppx) {
      background-image: url("../img/icons/icon_gift.svg"),
        url("../img/offer/catbag-large@2x.jpg");
    }
  }
}
.offer__text {
  margin: 0;
}
