.page-footer {
  padding: 40px 20px 20px 20px;
  background-color: $color-gray-50;
  @media (min-width: $breakpoint-medium) {
    padding: 60px 0;
  }
}
.page-footer__container {
  @media (min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: 164px 1fr 164px;
    align-items: center;
  }
}
