.promo {
  background-color: $color-primary-500;
  background-image: url("../../img/promo/background.jpg"),
    linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);
  background-repeat: no-repeat;
  text-align: center;
  padding: 20px;
  color: $color-white;
  @media (min-width: $breakpoint-medium) {
    text-align: left;
    background-color: $color-white;
    background-image: none;
    color: $color-black;
    padding-top: 40px;
  }
  @media (min-width: $breakpoint-large) {
    position: relative;
    padding: 0;
    &::after {
      content: "";
      position: absolute;
      top: -200px;
      right: 0;
      bottom: 0;
      left: 50%;
      z-index: 0;
      background-color: #63a63b;
      background-position: 0 100%;
      background-repeat: no-repeat;
      background-image: url("../../img/promo/background_large\ .jpg");
    }
  }
}
.promo__container {
  position: relative;
  @media (min-width: $breakpoint-medium) {
    padding: 0 90px 530px;
  }
  @media (min-width: $breakpoint-large) {
    padding: 80px 80px 185px 80px;
  }
}
.promo__title {
  font-family: $font-heading;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  margin-bottom: 25px;
  @media (min-width: $breakpoint-medium) {
    font-size: 60px;
    line-height: 60px;
    max-width: 415px;
    font-weight: 400;
    margin-bottom: 40px;
  }
}
.promo__img {
  width: 280px;
  height: 270px;
  @media (min-width: $breakpoint-medium) {
    width: 709px;
    height: 609px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
  @media (min-width: $breakpoint-large) {
    width: 552px;
    height: 499px;
    left: calc(50% - 140px);
    z-index: 2;
  }
}
.promo_description {
  font-family: $font-heading;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 30px;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
}
.promo__button {
  width: 100%;
  transition: all 0.2s ease;
  &:hover {
    background-color: #5eaa2f;
  }
  &:active {
    color: rgba($color: #fff, $alpha: 0.3);
  }
  @media (min-width: $breakpoint-medium) {
    max-width: 245px;
    padding: 14px 26px;
    position: relative;
    z-index: 2;
  }
}
