.page__main--inner {
  @media (max-width: $breakpoint-medium) {
    padding-top: 27px;
  }
}

.page-main__title {
  font-family: $font-heading;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin: 0;
  padding-bottom: 40px;
  @media (min-width: $breakpoint-medium) {
    font-size: 60px;
    line-height: 1;
    padding: 28px 0 40px;
  }
}
.page-main__subtitle {
  font-family: $font-heading;
  color: $color-black;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: 60px;
    margin-top: 15px;
    font-size: 20px;
    line-height: 30px;
  }
}
