.site-menu {
  font-family: $font-heading;
  list-style: none;
  font-size: 20px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0;
  font-weight: 500;
  @media (max-width: $breakpoint-small-down) {
    position: absolute;
    left: 0;
    right: 0;
    background-color: $color-white;
    z-index: 1;
    top: -100%;
    transition: all 0.3s ease;
  }
  @media (min-width: $breakpoint-medium) {
    display: flex;
    font-size: 18px;
    justify-content: end;
    column-gap: 33px;
    font-weight: 400;
    flex-wrap: wrap;
  }
  @media (min-width: $breakpoint-large) {
    font-size: 20px;
  }
}
.site-menu__item {
  @media (max-width: $breakpoint-small-down) {
    border-top: 1px solid #e6e6e6;
  }
}

.site-menu__item--active {
  position: relative;
  @media (min-width: $breakpoint-medium) {
    &::after {
      content: "";
      height: 2px;
      position: absolute;
      background-color: $color-primary-500;
      left: 0;
      right: 0;
      bottom: -10px;
    }
  }
  @media (min-width: $breakpoint-large) {
    &_main::after {
      background-color: $color-white;
    }
  }
}
.site-menu__link {
  text-decoration: none;
  color: $color-black;
  display: block;
  padding: 23px 22px 22px;
  &:link:hover {
    opacity: 0.6;
  }

  &:link:active {
    opacity: 0.3;
  }
  @media (min-width: $breakpoint-medium) {
    padding: 0;
  }
  @media (min-width: $breakpoint-large) {
    &--main {
      color: $color-white;
    }
  }
}
