.form__container {
  @media (min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
  .form__group--target {
    padding: 20px 0;
    position: relative;
    @media (min-width: $breakpoint-medium) {
      padding: 0;
    }
  }
}
.form__group {
  margin: 0;
  & + & {
    margin-top: 25px;
  }
  &--target {
    &::after,
    &::before {
      content: "";
      position: absolute;
      height: 1px;
      background-color: #d9d9d9;
      width: 100vw;
      left: calc(50% - 50vw);
    }
    &::before {
      top: 0;
    }
    &::after {
      bottom: 0;
    }
    @media (min-width: $breakpoint-medium) {
      &::after,
      &::before {
        height: 0;
      }
    }
    .field-group__list {
      @media (min-width: $breakpoint-medium) {
        border: 1px solid #d9d9d9;
        padding: 25px;
      }
      @media (min-width: $breakpoint-large) {
        border: none;
        padding: 0;
        padding-left: 30px;
        display: flex;
        flex-direction: column;
      }
    }
  }
  @media (min-width: $breakpoint-medium) {
    & + & {
      margin-top: 70px;
    }
  }
  @media (min-width: $breakpoint-large) {
    & + &--target {
      margin-top: 0;
      border: 1px solid #d9d9d9;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
}

.form__group--contacts {
  @media (min-width: $breakpoint-large) {
    grid-column: 1/-1;
  }
}
.form__group--comment {
  @media (min-width: $breakpoint-large) {
    grid-column: 1/-1;
  }
}
.form__group--additional {
  @media (min-width: $breakpoint-large) {
    grid-column: 1/-1;
  }
}
.form__footer {
  margin-top: 35px;
  padding-bottom: 35px;
  text-align: center;
  @media (min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: 280px 180px;
    gap: 115px;
    align-items: center;
    margin-top: 55px;
    padding-bottom: 70px;
  }
}
.form__additional {
  text-align: left;
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    font-size: 16px;
  }
}
.form__button {
  width: 100%;
  margin-bottom: 20px;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: 0;
  }
}
textarea {
  resize: none;
}
