.location {
  padding: 0;
  position: relative;
  &:not(.location--main) {
    background-color: $color-gray-50;
  }
  @media (min-width: $breakpoint-large) {
    position: relative;
    z-index: 0;
    min-height: 400px;
    padding: 40px 0 52px;
    background-color: transparent;
  }
  @media (min-width: $breakpoint-large) {
    .location__container {
      width: 565px;
      position: absolute;
      background-color: $color-white;
      padding: 56px 80px;
      top: 95px;
      left: 110px;
    }
  }
}
.location__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 26px 0;
  @media (min-width: $breakpoint-medium) {
    width: 708px;
    padding: 56px 40px;
    grid-template-columns: 172px 172px;
    justify-content: space-between;
    position: relative;
    z-index: 1;
  }
}
.location__title {
  font-family: $font-heading;
  color: $color-gray-150;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    max-width: 170px;
    font-size: 20px;
    line-height: 26px;
  }
}
.location__address {
  color: $color-dark-gray;
  line-height: 20px;
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    max-width: 170px;
    text-align: right;
    font-size: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
  }
}
.location__map {
  display: block;
  width: 100%;
  height: 362px;

  border: none;

  @media (min-width: $breakpoint-medium) {
    height: 400px;
  }

  @media (min-width: $breakpoint-large) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}
