.field {
  color: $color-dark-gray;

  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  column-gap: 15px;
  position: relative;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 30px;
    column-gap: 18px;
  }
}

.field__input {
  font-family: inherit;
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  padding: 9px 14px;
  border: 1px solid #e6e6e6;
  &:hover {
    border-color: $color-gray-180;
  }

  &:focus {
    outline: 2px solid $color-dark-gray;
    outline-offset: -2px;
  }
  &:not(:placeholder-shown):invalid {
    outline: 2px solid $color-error-500;
    outline-offset: -2px;
  }
  &::placeholder {
    opacity: 1;
  }
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 30px;
  }
}
.field--comment {
  margin-top: 35px;
  display: block;
  width: 100%;
  .field__input--comment {
    width: 100%;
  }
}
.field__icon {
  position: absolute;
  right: 8px;
}
