.field-group {
  border: none;
  padding: 0;
}
.field-group__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 15px;
  clear: both;
  @media (min-width: $breakpoint-medium) {
    row-gap: 20px;
  }
}
.field-group__list--contacts,
.field-group__list--additional {
  margin-top: 35px;
  @media (min-width: $breakpoint-medium) {
    margin-top: 0;
  }
}
.field-group__list--contacts {
  @media (min-width: $breakpoint-large) {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
}
.field-group__title {
  font-family: $font-heading;
  font-size: 24px;
  font-weight: 400;
  line-height: 26px;
  display: flex;
  padding: 0;
  float: left;
  margin-bottom: 35px;
  &::before {
    content: "";
    position: absolute;
    right: 0;
    left: 0;
    height: 2px;
    margin-top: 16px;
    background-color: $color-primary-500;
  }
  span {
    position: relative;
    z-index: 1;
    width: -webkit-min-content;
    width: -moz-min-content;
    width: min-content;
    margin-left: -22px;
    padding: 0 22px;
    background-color: #ffffff;
  }
  @media (min-width: $breakpoint-medium) {
    font-size: 36px;
    line-height: 1;
    margin-bottom: 70px;
    span {
      width: -webkit-max-content;
      width: -moz-max-content;
      width: max-content;
    }
    &::before {
      margin-top: 20px;
    }
  }
}
.field-group__list--additional {
  @media (min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
  }
  @media (min-width: $breakpoint-large) {
    grid-template-columns: repeat(4, 1fr);
  }
}
