.logo:not(.logo--footer) {
  display: grid;
  grid-template-columns: 34px 1fr;
  align-items: center;
  gap: 57px;
  @media (min-width: $breakpoint-medium) {
    gap: 16px;
    grid-template-columns: 61px 1fr;
  }
  @media (min-width: $breakpoint-medium) {
    grid-template-columns: 71px 1fr;
  }
}
.logo__title {
  width: 101px;
  height: auto;
  @media (min-width: $breakpoint-medium) {
    width: 102px;
  }
  @media (min-width: $breakpoint-medium) {
    width: 118px;
  }
}
.logo--footer {
  display: flex;
  justify-content: center;
  .logo__image {
    width: 128px;
    height: 24px;
    margin-bottom: 30px;
  }
  @media (min-width: $breakpoint-medium) {
    .logo__image {
      margin-bottom: 0;
    }
  }
}
.logo__image {
  width: 34px;
  @media (min-width: $breakpoint-medium) {
    width: 61px;
  }
  @media (min-width: $breakpoint-large) {
    width: 71px;
  }
}
