.catalog-additional {
  padding: 25px 0 20px;
  position: relative;
  z-index: 0;
  &::before {
    content: "";
    position: absolute;
    height: 2px;
    background-color: $color-primary-500;
    right: 0;
    left: 0;
    top: 40px;
    z-index: -1;
  }
  @media (min-width: $breakpoint-medium) {
    padding: 80px 0 80px;
    &::before {
      top: 100px;
    }
  }
}
.catalog-additional__container {
  @media (min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}
.catalog-additional__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  background-color: $color-white;
  margin-left: -20px;
  margin-bottom: 36px;
  padding: 0 20px;
  width: 270px;
  @media (min-width: $breakpoint-medium) {
    font-size: 36px;
    width: 380px;
    margin-bottom: 80px;
    margin-left: 0;
  }
  @media (min-width: $breakpoint-large) {
    grid-column: 1/-1;
  }
}
.catalog-additional__list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
  @media (min-width: $breakpoint-medium) {
    border: 1px solid $color-gray-250;
  }
  @media (min-width: $breakpoint-large) {
    grid-column: 1/4;
    border: none;
    margin-bottom: 0;
  }
}
.catalog-additional__item {
  border-top: 1px solid $color-gray-250;
  @media (max-width: $breakpoint-small-down) {
    &:last-child {
      border-bottom: 1px solid $color-gray-250;
    }
  }
  @media (min-width: $breakpoint-medium) {
    &:first-child {
      border: none;
    }
  }
  @media (min-width: $breakpoint-large) {
    &:first-child {
      border-top: 1px solid $color-gray-250;
    }
    &:last-child {
      border-bottom: 1px solid $color-gray-250;
    }
  }
}
