.range {
  display: grid;
  grid-template-columns: 49px 1fr 49px;
  gap: 13px;
  align-items: center;
  padding: 0 35px;

  @media (min-width: $breakpoint-medium) {
    padding: 0 70px;
  }
}

.range__button {
  font-family: $font-heading;
  color: $color-black;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: inherit;
  border: none;
  cursor: pointer;
  @media (min-width: $breakpoint-medium) {
    border-bottom: 1px dashed rgba($color: #000000, $alpha: 0.3);
    padding: 0;
    font-size: 20px;
  }
}
.range__switcher {
  background-color: $color-white;
  height: 14px;
  border-radius: 13px;
  position: relative;
  display: grid;
  align-items: center;
  padding: 0 6px;
  &::after {
    content: "";
    height: 6px;
    width: 35px;
    background-color: $color-primary-500;
    border-radius: 6px;
  }
  @media (min-width: $breakpoint-small-down) {
    display: none;
  }
}

.range__bar {
  appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
  @media (max-width: $breakpoint-small-down) {
    display: none;
  }
}
.range__bar::-moz-range-thumb {
  height: 30px;
  width: 30px;
  padding: 0;
  box-sizing: border-box;

  border: 11px solid $color-white;
  border-radius: 50%;
  background-color: $color-primary-500;
  box-shadow: 0 0 0 1px $color-gray-250, 0 1px 3px rgba($color-black, 0.16);
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 0 1px #cdcdcd, 0 2px 4px rgba($color-black, 0.19);
  }

  &:active {
    box-shadow: 0 0 0 1px $color-primary-500, 0 3px 5px rgba($color-black, 0.2);
  }
}
.range__bar::-webkit-slider-thumb {
  appearance: none;

  height: 30px;
  width: 30px;
  margin-top: -12px;
  padding: 0;
  box-sizing: border-box;

  border: 11px solid $color-white;
  border-radius: 50%;
  background-color: $color-primary-500;
  box-shadow: 0 0 0 1px $color-gray-250, 0 1px 3px rgba($color-black, 0.16);
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 0 1px $color-gray-250, 0 2px 4px rgba($color-black, 0.19);
  }

  &:active {
    box-shadow: 0 0 0 1px $color-primary-500, 0 3px 5px rgba($color-black, 0.2);
  }
}
.range__bar::-ms-thumb {
  appearance: none;

  height: 30px;
  width: 30px;
  margin-top: -12px;
  padding: 0;
  box-sizing: border-box;

  border: 11px solid $color-white;
  border-radius: 50%;
  background-color: $color-primary-500;
  box-shadow: 0 0 0 1px $color-gray-250, 0 1px 3px rgba($color-black, 0.16);
  cursor: pointer;

  &:hover {
    box-shadow: 0 0 0 1px $color-gray-250, 0 2px 4px rgba($color-black, 0.19);
  }

  &:active {
    box-shadow: 0 0 0 1px $color-primary-500, 0 3px 5px rgba($color-black, 0.2);
  }
}
.range__bar::-webkit-slider-runnable-track,
.range__bar::-moz-range-track {
  height: 8px;
  background-color: $color-gray-180;
  border-radius: 6px;
}

.range__bar::-webkit-slider-runnable-track {
  height: 8px;
  background-color: $color-gray-180;
  border-radius: 6px;
}
