.example {
  background-color: $color-gray-100;
  padding: 25px 20px 40px 20px;
  @media (min-width: $breakpoint-medium) {
    border-top: 1px solid #e6e6e6;
    background: linear-gradient(
      to top,
      $color-gray-100 460px,
      $color-white 460px
    );
  }
  @media (min-width: $breakpoint-large) {
    border: none;
    padding-bottom: 75px;
  }
}
.example__container {
  @media (min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: 436px 600px;
    column-gap: 193px;
  }
}
.example__content {
  margin-bottom: 40px;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: 80px;
  }
  @media (min-width: $breakpoint-large) {
    margin-bottom: 0;
  }
}
.example__text {
  color: $color-dark-gray;
  margin: 0;
  margin-bottom: 20px;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: 54px;
    font-size: 16px;
    line-height: 24px;
  }
  @media (min-width: $breakpoint-large) {
    margin-top: 146px;
  }
}
.example__info {
  @media (min-width: $breakpoint-medium) {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  @media (min-width: $breakpoint-large) {
    flex-direction: column;
    align-items: start;
    row-gap: 60px;
  }
}
.example__list {
  text-align: center;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 32px;
  margin: 0;
  margin-bottom: 35px;
  @media (min-width: $breakpoint-medium) {
    margin: 0;
  }
}
.example__item {
  padding: 15px 10px 5px;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  @media (min-width: $breakpoint-medium) {
    min-width: 162px;
  }
}
.example__item-wrapper {
  display: flex;
  flex-direction: column-reverse;
  margin-bottom: -18px;
  row-gap: 8px;
  align-items: center;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: -12px;
  }
}
.example__title {
  font-size: 12px;
  color: $color-dark-gray;
  text-transform: lowercase;
  background-color: $color-gray-100;
  max-width: 80px;
  line-height: 1;
  padding: 0 5px;
  @media (min-width: $breakpoint-medium) {
    font-size: 14px;
    line-height: 1;
    max-width: 100%;
    padding: 0 10px;
    background-color: $color-white;
  }
  @media (min-width: $breakpoint-large) {
    background-color: $color-gray-100;
  }
}
.example__definition {
  font-family: $font-heading;
  color: $color-black;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1;
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    font-size: 30px;
    line-height: 37px;
  }
}
.example__spending {
  font-family: $font-heading;
  color: $color-black;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px;
  text-align: center;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 20px;
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    span + span {
      align-self: flex-end;
    }
  }
  @media (min-width: $breakpoint-large) {
    flex-direction: row;
    column-gap: 60px;
  }
}
.example__images {
  margin-bottom: 15px;
  @media (min-width: $breakpoint-medium) {
    display: grid;
    grid-template-columns: 50% 1fr;
    padding: 0 58px;
    overflow: hidden;
  }
  @media (min-width: $breakpoint-large) {
    padding: 0;
  }
}
.example__image {
  display: none;
  &--active {
    display: block;
  }
  @media (min-width: $breakpoint-medium) {
    overflow: hidden;
    display: flex;
    & img {
      width: 594px;
      height: 511px;
      max-width: none;
    }
    &--after {
      justify-content: end;
    }
  }
}
