.page__header--inner {
  @media (max-width: $breakpoint-medium) {
    border-bottom: 1px solid #d9d9d9;
  }
}

.page-header__nav {
  &.active {
    .page-header__list {
      top: 66px;
    }
  }
  @media (min-width: $breakpoint-medium) {
    width: 708px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: max-content 1fr;
    gap: 16px;
    align-items: center;
    padding: 25px 0;
  }

  @media (min-width: $breakpoint-large) {
    width: 1220px;
    position: relative;
    z-index: 2;
    color: $color-white;
    padding: 70px 0 40px 0;
    grid-template-columns: max-content 50%;
    justify-content: space-between;
  }
}
.page-header__bar {
  display: grid;
  grid-template-columns: 1fr 40px;
  max-width: 320px;
  margin: 0 auto;
  align-items: center;
  padding: 10px 20px 18px 20px;
  position: relative;
  z-index: 3;

  @media (max-width: $breakpoint-small-down) {
    background-color: $color-white;
  }
  @media (min-width: $breakpoint-medium) {
    padding: 0;
    display: block;
  }
}

.page-header__toggle {
  width: 24px;
  height: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
  &::after,
  &::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    background-color: black;
  }
  &::after {
    top: 1px;
  }
  &::before {
    bottom: 0;
  }
  &.active::after {
    transform: rotate(-45deg);
    top: 8px;
  }
  &.active::before {
    transform: rotate(45deg);
    top: 8px;
  }

  .page-header__toggle-row {
    position: absolute;
    background-color: black;
    left: 0;
    width: 100%;
    height: 1px;
  }
  &.active {
    .page-header__toggle-row {
      transform: scale(0);
    }
  }
  @media (min-width: $breakpoint-medium) {
    display: none;
  }
}
