.social {
  padding: 0;
  margin: 0;
  list-style: none;
  padding: 12px 0;
  margin-bottom: 22px;
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  list-style: none;
  @media (min-width: $breakpoint-medium) {
    margin-bottom: 0;
    padding: 0;
    border: 0;
  }
}
.social__item {
  margin: 0 4px;
  display: flex;
  align-items: center;
}
.social__link {
  display: block;
  padding: 8px;
  color: #666666;
  svg {
    display: block;
    width: 28px;
    height: 22px;
  }
  &:hover {
    color: $color-primary-500;
  }
}
