.show-more {
  text-align: center;
  padding: 80px 0 25px;
  position: relative;
  background-image: url("../img/show__more/plus.svg");
  background-repeat: no-repeat;
  background-position: 50% 15%;
  &::before {
    content: "";
    position: absolute;
    width: 100vw;
    top: 0;
    left: calc(50% - 50vw);
    height: 1px;
    background-color: $color-gray-300;
  }
  @media (min-width: $breakpoint-medium) {
    border: 2px solid $color-gray-50;
    padding: 0;
    margin-top: 80px;
    padding: 160px 38px 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-image: url("../img/show__more/plus-large.svg");
    &::before {
      height: 0;
    }
  }
}
.show-more__title {
  font-family: $font-heading;
  color: $color-gray-150;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 8px;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 22px;
  }
}
.show-more__text {
  font-size: 12px;
  line-height: 16px;
  color: $color-dark-gray;
  margin: 0;
  margin-bottom: 20px;
  @media (min-width: $breakpoint-medium) {
    font-size: 16px;
    line-height: 18px;
  }
}
.show-more__button {
  width: 100%;
}
