.card {
  display: grid;
  padding-top: 19px;
  padding-bottom: 25px;
  @media (max-width: $breakpoint-small-down) {
    grid-template-columns: 1fr 1fr;
    grid-template-areas: "img link" "img list" "btn btn";
    position: relative;
  }
  @media (min-width: $breakpoint-medium) {
    padding: 8px 38px 40px;
    grid-template-rows: 200px auto;
    grid-template-areas:
      "img"
      "link"
      "list"
      "btn";
    background-color: #f2f2f2;
    background-image: linear-gradient(to bottom, #ffffff 80px, #f2f2f2 80px);
  }
  &::after {
    content: "";
    position: absolute;
    width: 100vw;
    top: 0;
    left: calc(50% - 50vw);
    height: 1px;
    background-color: $color-gray-300;
  }
}
.card__link {
  grid-area: link;
  text-decoration: none;
  color: #222;
  padding-right: 50px;
  margin-bottom: 14px;
  @media (min-width: $breakpoint-medium) {
    padding: 0px 60px;
    text-align: center;
  }
  @media (min-width: $breakpoint-large) {
    padding: 0px 20px;
  }
}
.card__title {
  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }
}
.card__list {
  color: #444444;
  font-size: 12px;
  margin: 0;
  grid-area: list;
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 2px;
  margin-bottom: 22px;
  padding: 9px 0;
  @media (min-width: $breakpoint-medium) {
    font-size: 16px;
    line-height: 1;
  }
}
.card__subtitle {
  margin: 0;
  @media (min-width: $breakpoint-medium) {
    border-bottom: 1px solid $color-gray-250;
    padding-bottom: 9px;
    padding-top: 7px;
  }
}
.card__definition {
  margin: 0;
  text-align: right;
  @media (min-width: $breakpoint-medium) {
    border-bottom: 1px solid $color-gray-250;
    padding-bottom: 9px;
    padding-top: 7px;
  }
}
.card__picture,
.card__img {
  grid-area: img;
  align-self: center;
  justify-self: center;

  @media (min-width: $breakpoint-medium) {
    width: auto;
  }
}
.card__button {
  grid-area: btn;
  font-size: 16px;
}
