.process {
  padding: 20px;
  @media (min-width: $breakpoint-medium) {
    padding-top: 45px;
  }
  @media (min-width: $breakpoint-large) {
    padding-top: 70px;
  }
}
.process__list {
  list-style: none;
  color: $color-dark-gray;
  margin: 0;
  padding: 0;
  @media (min-width: $breakpoint-medium) {
    counter-reset: process;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 60px;
    row-gap: 30px;
  }
  @media (min-width: $breakpoint-large) {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}
.process__item + .process__item {
  margin-top: 25px;
  @media (min-width: $breakpoint-medium) {
    margin: 0;
  }
}
.process__item {
  display: grid;
  grid-template-columns: 60px 1fr;
  column-gap: 20px;
  &::before {
    content: "";
    width: 60px;
    height: 60px;
    background-color: $color-primary-500;
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
  @media (min-width: $breakpoint-medium) {
    grid-template-columns: auto;
    grid-template-rows: 80px auto;
    row-gap: 30px;
    counter-increment: process;
    position: relative;
    padding-top: 32px;
    padding-right: 70px;
    min-height: 280px;
    font-size: 16px;
    line-height: 24px;
    &::before {
      width: 80px;
      height: 80px;
    }
    &::after {
      content: counter(process);
      position: absolute;
      font-size: 280px;
      color: $color-gray-50;
      font-family: $font-heading;
      z-index: -1;
      top: 120px;
      right: 80px;
    }
  }
  @media (min-width: $breakpoint-large) {
    padding-right: 0;
    &::after {
      right: 0;
    }
  }
}
.process__item--leaf::before {
  background-image: url("../img/process/1.svg");
}
.process__item--powder::before {
  background-image: url("../img/process/2.svg");
}
.process__item--meal::before {
  background-image: url("../img/process/3.svg");
}
.process__item--weight::before {
  background-image: url("../img/process/4.svg");
}
