@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  src: url("../fonts/oswaldregular.woff") format("woff2");
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

img {
  max-width: 100%;
  height: auto;
}

.button {
  font-family: "Oswald", "Arial", sans-serif;
  color: #fff;
  background-color: #68b738;
  text-decoration: none;
  text-transform: uppercase;
  border: none;
  display: inline-block;
  text-align: center;
  padding: 11px 20px;
}
@media (min-width: 768px) {
  .button {
    padding: 14px 26px;
    font-size: 20px;
    line-height: 1;
  }
}
.button:hover {
  background-color: #5eaa2f;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5eaa2f;
}

.button--secondary {
  color: #444444;
  background-color: #f2f2f2;
}

.button--fixed {
  font-size: 16px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.site-menu {
  font-family: "Oswald", "Arial", sans-serif;
  list-style: none;
  font-size: 20px;
  line-height: 1;
  text-transform: uppercase;
  text-align: center;
  padding: 0;
  margin: 0;
  font-weight: 500;
}
@media (max-width: 767px) {
  .site-menu {
    position: absolute;
    left: 0;
    right: 0;
    background-color: #fff;
    z-index: 1;
    top: -100%;
    transition: all 0.3s ease;
  }
}
@media (min-width: 768px) {
  .site-menu {
    display: flex;
    font-size: 18px;
    justify-content: end;
    column-gap: 33px;
    font-weight: 400;
    flex-wrap: wrap;
  }
}
@media (min-width: 1300px) {
  .site-menu {
    font-size: 20px;
  }
}

@media (max-width: 767px) {
  .site-menu__item {
    border-top: 1px solid #e6e6e6;
  }
}

.site-menu__item--active {
  position: relative;
}
@media (min-width: 768px) {
  .site-menu__item--active::after {
    content: "";
    height: 2px;
    position: absolute;
    background-color: #68b738;
    left: 0;
    right: 0;
    bottom: -10px;
  }
}
@media (min-width: 1300px) {
  .site-menu__item--active_main::after {
    background-color: #fff;
  }
}

.site-menu__link {
  text-decoration: none;
  color: #000000;
  display: block;
  padding: 23px 22px 22px;
}
.site-menu__link:link:hover {
  opacity: 0.6;
}
.site-menu__link:link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .site-menu__link {
    padding: 0;
  }
}
@media (min-width: 1300px) {
  .site-menu__link--main {
    color: #fff;
  }
}

.page {
  min-width: 320px;
  height: 100%;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
}

.page__body {
  min-height: 100%;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}

@media (max-width: 768px) {
  .page__main--inner {
    padding-top: 27px;
  }
}

.page-main__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 40px;
  margin: 0;
  padding-bottom: 40px;
}
@media (min-width: 768px) {
  .page-main__title {
    font-size: 60px;
    line-height: 1;
    padding: 28px 0 40px;
  }
}

.page-main__subtitle {
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  line-height: 18px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .page-main__subtitle {
    margin-bottom: 60px;
    margin-top: 15px;
    font-size: 20px;
    line-height: 30px;
  }
}

.promo {
  background-color: #68b738;
  background-image: url("../../img/promo/background.jpg"), linear-gradient(#63a63b 0, #63a63b 302px, #ffffff 302px);
