    padding: 0 90px 530px;
  }
}
@media (min-width: 1300px) {
  .promo__container {
    padding: 80px 80px 185px 80px;
  }
}

.promo__title {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  margin: 0;
  margin-bottom: 25px;
}
@media (min-width: 768px) {
