  height: 270px;
}
@media (min-width: 768px) {
  .promo__img {
    width: 709px;
    height: 609px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}
@media (min-width: 1300px) {
  .promo__img {
    width: 552px;
    height: 499px;
    left: calc(50% - 140px);
    z-index: 2;
  }
}

.promo_description {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  line-height: 14px;
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .promo_description {
    font-size: 20px;
    line-height: 1;
    text-transform: uppercase;
    margin-bottom: 50px;
  }
