}
@media (min-width: 768px) {
  .promo__button {
    max-width: 245px;
    padding: 14px 26px;
    position: relative;
    z-index: 2;
  }
}

@media (min-width: 1300px) {
  .catalog {
    padding-top: 80px;
  }
}

@media (min-width: 1300px) {
  .catalog__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}

.catalog__item {
  background-color: #f2f2f2;
  padding: 20px;
  width: 100%;
}
@media (min-width: 768px) {
  .catalog__item {
    padding: 75px 60px 60px 50px;
    position: relative;
  }
  .catalog__item::before {
    content: "";
    width: 200px;
    height: 200px;
    background-color: #68b738;
    position: absolute;
    border-radius: 50%;
    right: 60px;
    top: 50%;
    transform: translateY(-100px);
  }
  .catalog__item--slim::after {
    content: "";
    width: 140px;
    height: 194px;
    background-image: url("../img/catalog_main/tablet/cat-slim.svg");
    position: absolute;
    right: 92px;
    top: 50%;
    transform: translateY(-115px);
  }
  .catalog__item--pro::after {
    content: "";
    width: 268px;
    height: 112px;
    background-image: url("../img/catalog_main/tablet/cat-pro.svg");
    position: absolute;
    right: 27px;
    top: 50%;
    transform: translateY(-55px);
  }
}
@media (min-width: 1300px) {
  .catalog__item {
    padding: 80px 50px 60px;
  }
  .catalog__item::before {
    width: 0;
    height: 0;
  }
  .catalog__item--slim::after, .catalog__item--pro::after {
    width: 0;
    height: 0;
  }
}

.catalog__item + .catalog__item {
  margin-top: 20px;
}
@media (min-width: 1300px) {
  .catalog__item + .catalog__item {
    margin: 0;
  }
}

.catalog__title {
  color: #000000;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  position: relative;
  padding-left: 70px;
  display: flex;
  align-items: center;
}
.catalog__title::before {
  content: "";
  width: 50px;
  height: 50px;
  background-color: #68b738;
  position: absolute;
  left: 0;
  border-radius: 50%;
}
.catalog__title--slim::after {
  content: "";
  width: 36px;
  height: 50px;
  background-image: url("../img/catalog_main/cat-slim.svg");
  position: absolute;
  left: 6px;
}
.catalog__title--pro::after {
  content: "";
  width: 67px;
  height: 28px;
  background-image: url("../img/catalog_main/cat-pro.svg");
  position: absolute;
  left: -9px;
}
@media (min-width: 768px) {
  .catalog__title {
    padding-left: 0;
    font-size: 36px;
    line-height: 1;
  }
  .catalog__title::before {
    width: 0px;
    height: 0px;
  }
  .catalog__title--slim::after, .catalog__title--pro::after {
    width: 0;
    height: 0;
  }
}
@media (min-width: 1300px) {
  .catalog__title {
    padding-left: 160px;
    margin-bottom: 65px;
  }
  .catalog__title::before {
    width: 100px;
    height: 100px;
  }
  .catalog__title--slim::after {
    content: "";
    width: 70px;
    height: 97px;
    background-image: url("../img/catalog_main/desktop/cat-slim.svg");
    position: absolute;
    left: 15px;
    top: -35px;
  }
  .catalog__title--pro::after {
    content: "";
    width: 134px;
    height: 56px;
    background-image: url("../img/catalog_main/desktop/cat-pro.svg");
    position: absolute;
    left: -17px;
  }
}

.catalog__text {
  color: #444;
  margin: 0;
  margin-top: 30px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .catalog__text {
    max-width: 340px;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
    font-weight: 400;
    border-bottom: none;
  }
}
@media (min-width: 1300px) {
  .catalog__text {
    max-width: 100%;
  }
}

.catalog_link {
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 16px;
  display: inline-block;
  margin-top: 15px;
  display: grid;
  grid-template-columns: max-content 32px;
  align-items: center;
  gap: 16px;
}
.catalog_link::after {
  content: "";
  width: 24px;
  height: 10px;
  background-repeat: no-repeat;
  background-position: 100% 50%;
  background-image: url("../img/catalog_main/icon_arrow.svg");
  transition: width 0.2s ease;
}
.catalog_link:hover::after {
  width: 32px;
}
@media (min-width: 768px) {
  .catalog_link {
    font-size: 20px;
  }
}

.process {
  padding: 20px;
}
@media (min-width: 768px) {
  .process {
    padding-top: 45px;
  }
}
@media (min-width: 1300px) {
  .process {
    padding-top: 70px;
  }
}

.process__list {
  list-style: none;
  color: #444;
  margin: 0;
  padding: 0;
}
@media (min-width: 768px) {
  .process__list {
    counter-reset: process;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 60px;
    row-gap: 30px;
  }
}
@media (min-width: 1300px) {
  .process__list {
    grid-template-columns: repeat(4, 1fr);
    column-gap: 80px;
  }
}

.process__item + .process__item {
  margin-top: 25px;
}
@media (min-width: 768px) {
  .process__item + .process__item {
    margin: 0;
  }
}

.process__item {
  display: grid;
  grid-template-columns: 60px 1fr;
  column-gap: 20px;
}
.process__item::before {
  content: "";
  width: 60px;
  height: 60px;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-position: 50% 50%;
}
@media (min-width: 768px) {
  .process__item {
    grid-template-columns: auto;
    grid-template-rows: 80px auto;
    row-gap: 30px;
    counter-increment: process;
    position: relative;
    padding-top: 32px;
    padding-right: 70px;
    min-height: 280px;
    font-size: 16px;
    line-height: 24px;
  }
  .process__item::before {
    width: 80px;
    height: 80px;
  }
  .process__item::after {
    content: counter(process);
    position: absolute;
    font-size: 280px;
    color: #f2f2f2;
    font-family: "Oswald", "Arial", sans-serif;
    z-index: -1;
    top: 120px;
    right: 80px;
  }
}
@media (min-width: 1300px) {
  .process__item {
    padding-right: 0;
  }
  .process__item::after {
    right: 0;
  }
}

.process__item--leaf::before {
  background-image: url("../img/process/1.svg");
}

.process__item--powder::before {
  background-image: url("../img/process/2.svg");
}

.process__item--meal::before {
  background-image: url("../img/process/3.svg");
}

.process__item--weight::before {
  background-image: url("../img/process/4.svg");
}

