.range {
  display: grid;
  grid-template-columns: 49px 1fr 49px;
  gap: 13px;
  align-items: center;
  padding: 0 35px;
}
@media (min-width: 768px) {
  .range {
    padding: 0 70px;
  }
}

.range__button {
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
  background-color: inherit;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .range__button {
    border-bottom: 1px dashed rgba(0, 0, 0, 0.3);
    padding: 0;
    font-size: 20px;
  }
