.range__switcher {
  background-color: #fff;
  height: 14px;
  border-radius: 13px;
  position: relative;
  display: grid;
  align-items: center;
  padding: 0 6px;
}
.range__switcher::after {
  content: "";
  height: 6px;
  width: 35px;
  background-color: #68b738;
  border-radius: 6px;
}
@media (min-width: 767px) {
  .range__switcher {
    display: none;
  }
}

.range__bar {
  appearance: none;
  /* Hides the slider so that custom slider can be made */
  width: 100%;
  /* Specific width is required for Firefox. */
  background: transparent;
  /* Otherwise white in Chrome */
}
@media (max-width: 767px) {
  .range__bar {
    display: none;
  }
}

.range__bar::-moz-range-thumb {
  height: 30px;
  width: 30px;
  padding: 0;
  box-sizing: border-box;
  border: 11px solid #fff;
  border-radius: 50%;
  background-color: #68b738;
  box-shadow: 0 0 0 1px #cdcdcd, 0 1px 3px rgba(0, 0, 0, 0.16);
  cursor: pointer;
}
.range__bar::-moz-range-thumb:hover {
  box-shadow: 0 0 0 1px #cdcdcd, 0 2px 4px rgba(0, 0, 0, 0.19);
}
.range__bar::-moz-range-thumb:active {
  box-shadow: 0 0 0 1px #68b738, 0 3px 5px rgba(0, 0, 0, 0.2);
}

.range__bar::-webkit-slider-thumb {
  appearance: none;
  height: 30px;
  width: 30px;
  margin-top: -12px;
  padding: 0;
  box-sizing: border-box;
  border: 11px solid #fff;
  border-radius: 50%;
  background-color: #68b738;
  box-shadow: 0 0 0 1px #cdcdcd, 0 1px 3px rgba(0, 0, 0, 0.16);
  cursor: pointer;
}
.range__bar::-webkit-slider-thumb:hover {
  box-shadow: 0 0 0 1px #cdcdcd, 0 2px 4px rgba(0, 0, 0, 0.19);
}
.range__bar::-webkit-slider-thumb:active {
  box-shadow: 0 0 0 1px #68b738, 0 3px 5px rgba(0, 0, 0, 0.2);
}

.range__bar::-ms-thumb {
  appearance: none;
  height: 30px;
  width: 30px;
  margin-top: -12px;
  padding: 0;
