.page-header__toggle {
  width: 24px;
  height: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  transition: all 0.2s ease;
}
.page-header__toggle::after, .page-header__toggle::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  background-color: black;
}
.page-header__toggle::after {
  top: 1px;
}
.page-header__toggle::before {
  bottom: 0;
}
.page-header__toggle.active::after {
  transform: rotate(-45deg);
  top: 8px;
}
.page-header__toggle.active::before {
  transform: rotate(45deg);
  top: 8px;
}
.page-header__toggle .page-header__toggle-row {
  position: absolute;
  background-color: black;
  left: 0;
  width: 100%;
  height: 1px;
}
.page-header__toggle.active .page-header__toggle-row {
  transform: scale(0);
}
@media (min-width: 768px) {
  .page-header__toggle {
    display: none;
  }
}

