.catalog {
  @media (min-width: $breakpoint-large) {
    padding-top: 80px;
  }
}
.catalog__container {
  @media (min-width: $breakpoint-large) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 80px;
  }
}
.catalog__item {
  background-color: $color-gray-50;
  padding: 20px;
  width: 100%;
  @media (min-width: $breakpoint-medium) {
    padding: 75px 60px 60px 50px;
    position: relative;
    &::before {
      content: "";
      width: 200px;
      height: 200px;
      background-color: $color-primary-500;
      position: absolute;
      border-radius: 50%;
      right: 60px;
      top: 50%;
      transform: translateY(-100px);
    }
    &--slim::after {
      content: "";
      width: 140px;
      height: 194px;
      background-image: url("../img/catalog_main/tablet/cat-slim.svg");
      position: absolute;
      right: 92px;
      top: 50%;
      transform: translateY(-115px);
    }
    &--pro::after {
      content: "";
      width: 268px;
      height: 112px;
      background-image: url("../img/catalog_main/tablet/cat-pro.svg");
      position: absolute;
      right: 27px;
      top: 50%;
      transform: translateY(-55px);
    }
  }
  @media (min-width: $breakpoint-large) {
    padding: 80px 50px 60px;
    &::before {
      width: 0;
      height: 0;
    }
    &--slim::after,
    &--pro::after {
      width: 0;
      height: 0;
    }
  }
}
.catalog__item + .catalog__item {
  margin-top: 20px;
  @media (min-width: $breakpoint-large) {
    margin: 0;
  }
}

.catalog__title {
  color: $color-black;
  font-family: $font-heading;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  margin: 0;
  position: relative;
  padding-left: 70px;
  display: flex;
  align-items: center;
  &::before {
    content: "";
    width: 50px;
    height: 50px;
    background-color: $color-primary-500;
    position: absolute;
    left: 0;
    border-radius: 50%;
  }
  &--slim::after {
    content: "";
    width: 36px;
    height: 50px;
    background-image: url("../img/catalog_main/cat-slim.svg");
    position: absolute;
    left: 6px;
  }
  &--pro::after {
    content: "";
    width: 67px;
    height: 28px;
    background-image: url("../img/catalog_main/cat-pro.svg");
    position: absolute;
    left: -9px;
  }
  @media (min-width: $breakpoint-medium) {
    padding-left: 0;
    font-size: 36px;
    line-height: 1;
    &::before {
      width: 0px;
      height: 0px;
    }
    &--slim::after,
    &--pro::after {
      width: 0;
      height: 0;
    }
  }
  @media (min-width: $breakpoint-large) {
    padding-left: 160px;
    margin-bottom: 65px;
    &::before {
      width: 100px;
      height: 100px;
    }
    &--slim::after {
      content: "";
      width: 70px;
      height: 97px;
      background-image: url("../img/catalog_main/desktop/cat-slim.svg");
      position: absolute;
      left: 15px;
      top: -35px;
    }
    &--pro::after {
      content: "";
      width: 134px;
      height: 56px;
      background-image: url("../img/catalog_main/desktop/cat-pro.svg");
      position: absolute;
      left: -17px;
    }
  }
}
.catalog__text {
  color: $color-dark-gray;
  margin: 0;
  margin-top: 30px;
  padding-bottom: 23px;
  border-bottom: 1px solid #d9d9d9;
  @media (min-width: $breakpoint-medium) {
    max-width: 340px;
    font-size: 16px;
    line-height: 24px;
    color: $color-black;
    font-weight: 400;
    border-bottom: none;
  }
  @media (min-width: $breakpoint-large) {
    max-width: 100%;
  }
}
.catalog_link {
  font-family: $font-heading;
  color: $color-black;
  font-size: 16px;
  text-decoration: none;
  text-transform: uppercase;
  line-height: 16px;
  display: inline-block;
  margin-top: 15px;
  display: grid;
  grid-template-columns: max-content 32px;
  align-items: center;
  gap: 16px;
  &::after {
    content: "";
    width: 24px;
    height: 10px;
    background-repeat: no-repeat;
    background-position: 100% 50%;
    background-image: url("../img/catalog_main/icon_arrow.svg");
    transition: width 0.2s ease;
  }
  &:hover::after {
    width: 32px;
  }
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
  }
}
