.card-row {
  padding: 13px 0 19px;
  @media (min-width: $breakpoint-medium) {
    padding: 26px 36px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 25px;
  }
  @media (min-width: $breakpoint-large) {
    padding: 15px 0;
    grid-template-columns: 1fr 80px 1fr 80px 1fr;
    align-items: center;
    column-gap: 0;
    row-gap: 0;
  }
}
.card-row__title {
  font-family: $font-heading;
  color: $special-dark-grey-2;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;
  font-weight: 400;
  margin: 0;
  margin-bottom: 13px;
  @media (min-width: $breakpoint-medium) {
    margin: 0;
    font-size: 20px;
  }
}
.card-row__list {
  color: $color-dark-gray;
  line-height: 1;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 8px;
  margin-bottom: 15px;
  @media (min-width: $breakpoint-medium) {
    margin: 0;
    font-size: 16px;
    line-height: 18px;
    text-align: right;
    grid-template-columns: 1fr max-content;
    column-gap: 50px;
  }
  @media (min-width: $breakpoint-large) {
    grid-column: 3/5;
    column-gap: 0px;
  }
}
.card-row__definition {
  margin: 0;
  &:last-child {
    text-align: right;
    @media (min-width: $breakpoint-large) {
      text-align: left;
    }
  }
  @media (min-width: $breakpoint-large) {
    text-align: left;
    padding-right: 40px;
  }
}
.card-row__button {
  width: 100%;
  @media (min-width: $breakpoint-medium) {
    grid-column: 1/-1;
  }
  @media (min-width: $breakpoint-large) {
    grid-column: 5/6;
  }
}
