.control {
  position: relative;
  display: grid;
  grid-template-columns: 30px 1fr;
  gap: 16px;
  color: $special-dark-grey-2;
  font-family: $font-heading;
  font-size: 16px;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  @media (min-width: $breakpoint-medium) {
    font-size: 20px;
    line-height: 1;
  }
}
.control__input {
  margin: 0;
  width: 100%;
  height: 30px;
  grid-row: 1 / 2;
  grid-column: 1 / 2;
  opacity: 0;
  @media (min-width: $breakpoint-medium) {
    height: 40px;
  }
}
.control__lead {
  display: grid;
  grid-row: 1 / 2;
  grid-column: 1/-1;
  grid-template-columns: 30px 1fr;
  gap: 16px;
  align-items: center;
  &::after,
  &::before {
    content: "";
    height: 30px;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
  }
  &::before {
    border: 1px solid $color-gray-250;
  }
}
.control--radio {
  .control__lead::before {
    border-radius: 50%;
  }
  .control__input:checked + .control__lead::after {
    background-clip: padding-box;
    background-color: #68b738;
    border: 8px solid transparent;
    border-radius: 50%;
  }
}
.control--checkbox {
  .control__input:checked + .control__lead::after {
    background-image: url("../img/input--checkbox/tick.svg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}
